<template>
  <view class="admin-container">
    <view class="page-header">
      <text class="page-title">ÂêàÂùõÊ≥ï‰ºö‰ª£ÂäûÁâ©ÂìÅÁÆ°ÁêÜ</text>
      <text class="page-desc">ÁÆ°ÁêÜÂêàÂùõÊ≥ï‰ºö‰ª£ÂäûÁâ©ÂìÅÂèä‰ª∑Ê†ºËÆæÁΩÆ</text>
      <button class="add-btn" @click="showAddModal">+ Êñ∞Â¢ûÁâ©ÂìÅ</button>
    </view>
    
    <!-- Ê®°ÂùóÂºÄÂÖ≥ -->
    <view class="module-switch">
      <view class="switch-item">
        <text class="switch-label">‰ª£ÂäûÁâ©ÂìÅÊ®°Âùó</text>
        <switch :checked="moduleEnabled" @change="onModuleToggle" color="#667eea" />
        <text class="switch-desc">{{ moduleEnabled ? 'Â∑≤ÂêØÁî®' : 'Â∑≤Á¶ÅÁî®' }}</text>
      </view>
    </view>
    
    <!-- Áâ©ÂìÅÂàóË°® -->
    <view v-if="moduleEnabled" class="goods-list">
      <view v-for="(good, index) in goods" :key="good._id" class="good-card">
        <view class="good-info">
          <view class="good-header">
            <text class="good-name">{{ good.name }}</text>
            <text class="good-price">¬•{{ good.price }}</text>
          </view>
          <view class="good-desc">{{ good.description || 'ÊöÇÊó†ÊèèËø∞' }}</view>
          <view class="good-details">
            <text class="detail-item">Â∫ìÂ≠òÔºö{{ good.stock || '‰∏çÈôê' }}</text>
            <text class="detail-item">Áä∂ÊÄÅÔºö{{ good.enabled ? 'ÂêØÁî®' : 'Á¶ÅÁî®' }}</text>
          </view>
        </view>
        
        <view class="good-actions">
          <button class="action-btn edit" @click="editGood(index)">ÁºñËæë</button>
          <button class="action-btn delete" @click="deleteGood(good._id)">Âà†Èô§</button>
        </view>
      </view>
    </view>
    
    <!-- Ê®°ÂùóÁ¶ÅÁî®ÊèêÁ§∫ -->
    <view v-else class="module-disabled">
      <view class="disabled-content">
        <text class="disabled-icon">üö´</text>
        <text class="disabled-title">‰ª£ÂäûÁâ©ÂìÅÊ®°ÂùóÂ∑≤Á¶ÅÁî®</text>
        <text class="disabled-desc">ÂêØÁî®ÂêéÂèØÂú®ÂâçÂè∞ÊòæÁ§∫‰ª£ÂäûÁâ©ÂìÅÈÄâÊã©ÂäüËÉΩ</text>
      </view>
    </view>
    
    <!-- Ê∑ªÂä†/ÁºñËæëÂºπÁ™ó -->
    <uni-popup ref="popup" type="center" :mask-click="false">
      <view class="popup-content">
        <view class="popup-header">
          <text class="popup-title">{{ isEdit ? 'ÁºñËæëÁâ©ÂìÅ' : 'Êñ∞Â¢ûÁâ©ÂìÅ' }}</text>
          <text class="popup-close" @click="closeModal">√ó</text>
        </view>
        
        <view class="form-content">
          <view class="form-item">
            <text class="form-label">Áâ©ÂìÅÂêçÁß∞ *</text>
            <input v-model="currentGood.name" class="form-input" placeholder="ËØ∑ËæìÂÖ•Áâ©ÂìÅÂêçÁß∞" />
          </view>
          
          <view class="form-item">
            <text class="form-label">Áâ©ÂìÅÊèèËø∞</text>
            <textarea v-model="currentGood.description" class="form-textarea" placeholder="ËØ∑ËæìÂÖ•Áâ©ÂìÅÊèèËø∞" maxlength="200" />
          </view>
          
          <view class="form-item">
            <text class="form-label">Ê≥ïÈáë‰ª∑Ê†º *</text>
            <input v-model="currentGood.price" type="number" class="form-input" placeholder="ËØ∑ËæìÂÖ•Ê≥ïÈáë‰ª∑Ê†º" />
          </view>
          
          <view class="form-item">
            <text class="form-label">Â∫ìÂ≠òÊï∞Èáè</text>
            <input v-model="currentGood.stock" type="number" class="form-input" placeholder="ËØ∑ËæìÂÖ•Â∫ìÂ≠òÊï∞ÈáèÔºàÁïôÁ©∫Ë°®Á§∫‰∏çÈôêÔºâ" />
          </view>
          
          <view class="form-item">
            <text class="form-label">Áä∂ÊÄÅ</text>
            <switch :checked="currentGood.enabled" @change="onGoodSwitchChange" color="#667eea" />
            <text class="switch-label">{{ currentGood.enabled ? 'ÂêØÁî®' : 'Á¶ÅÁî®' }}</text>
          </view>
        </view>
        
        <view class="form-actions">
          <button class="cancel-btn" @click="closeModal">ÂèñÊ∂à</button>
          <button class="save-btn" @click="saveGood">‰øùÂ≠ò</button>
        </view>
      </view>
    </uni-popup>
  </view>
</template>

<script>
import uniPopup from '@/components/uni-popup/uni-popup.vue'

export default {
  components: { uniPopup },
  
  data() {
    return {
      moduleEnabled: false,
      goods: [],
      isEdit: false,
      editIndex: -1,
      currentGood: {
        _id: '',
        name: '',
        description: '',
        price: '',
        stock: '',
        enabled: true
      }
    }
  },
  
  onLoad() {
    this.loadGoodsConfig()
  },
  
  methods: {
    async loadGoodsConfig() {
      try {
        const res = await uniCloud.callFunction({
          name: 'getJointGoodsConfig'
        })
        if (res.result && res.result.data) {
          this.moduleEnabled = res.result.data.enabled || false
          this.goods = res.result.data.goods || []
        }
      } catch (error) {
        console.error('Âä†ËΩΩÂêàÂùõÊ≥ï‰ºö‰ª£ÂäûÁâ©ÂìÅÈÖçÁΩÆÂ§±Ë¥•:', error)
        uni.showToast({ title: 'Âä†ËΩΩÂ§±Ë¥•', icon: 'none' })
      }
    },
    
    async onModuleToggle(e) {
      try {
        await uniCloud.callFunction({
          name: 'updateJointGoodsConfig',
          data: { 
            enabled: e.detail.value,
            goods: this.goods
          }
        })
        this.moduleEnabled = e.detail.value
        uni.showToast({ title: e.detail.value ? 'Ê®°ÂùóÂ∑≤ÂêØÁî®' : 'Ê®°ÂùóÂ∑≤Á¶ÅÁî®' })
      } catch (error) {
        console.error('Êõ¥Êñ∞Ê®°ÂùóÁä∂ÊÄÅÂ§±Ë¥•:', error)
        uni.showToast({ title: 'Êìç‰ΩúÂ§±Ë¥•', icon: 'none' })
      }
    },
    
    showAddModal() {
      this.isEdit = false
      this.editIndex = -1
      this.currentGood = {
        _id: '',
        name: '',
        description: '',
        price: '',
        stock: '',
        enabled: true
      }
      this.$refs.popup.open()
    },
    
    editGood(index) {
      this.isEdit = true
      this.editIndex = index
      this.currentGood = JSON.parse(JSON.stringify(this.goods[index]))
      this.$refs.popup.open()
    },
    
    closeModal() {
      this.$refs.popup.close()
    },
    
    onGoodSwitchChange(e) {
      this.currentGood.enabled = e.detail.value
    },
    
    async saveGood() {
      if (!this.currentGood.name || !this.currentGood.price) {
        uni.showToast({ title: 'ËØ∑Â°´ÂÜôÂøÖÂ°´È°π', icon: 'none' })
        return
      }
      
      try {
        const data = {
          ...this.currentGood,
          price: Number(this.currentGood.price) || 0,
          stock: this.currentGood.stock ? Number(this.currentGood.stock) : null
        }
        
        if (this.isEdit && data._id) {
          // Êõ¥Êñ∞Áâ©ÂìÅ
          const updateData = { ...data }
          delete updateData._id
          this.goods[this.editIndex] = { ...this.goods[this.editIndex], ...updateData }
        } else {
          // Êñ∞Â¢ûÁâ©ÂìÅ
          delete data._id
          data._id = Date.now().toString() // ‰∏¥Êó∂ID
          this.goods.push(data)
        }
        
        // ‰øùÂ≠òÂà∞‰∫ëÁ´Ø
        await uniCloud.callFunction({
          name: 'updateJointGoodsConfig',
          data: { 
            enabled: this.moduleEnabled,
            goods: this.goods
          }
        })
        
        this.closeModal()
        uni.showToast({ title: this.isEdit ? 'Êõ¥Êñ∞ÊàêÂäü' : 'Ê∑ªÂä†ÊàêÂäü' })
      } catch (error) {
        console.error('‰øùÂ≠òÂêàÂùõÊ≥ï‰ºö‰ª£ÂäûÁâ©ÂìÅÂ§±Ë¥•:', error)
        uni.showToast({ title: '‰øùÂ≠òÂ§±Ë¥•', icon: 'none' })
      }
    },
    
    async deleteGood(id) {
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™Áâ©ÂìÅÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            try {
              this.goods = this.goods.filter(good => good._id !== id)
              await uniCloud.callFunction({
                name: 'updateJointGoodsConfig',
                data: { 
                  enabled: this.moduleEnabled,
                  goods: this.goods
                }
              })
              uni.showToast({ title: 'Âà†Èô§ÊàêÂäü' })
            } catch (error) {
              console.error('Âà†Èô§ÂêàÂùõÊ≥ï‰ºö‰ª£ÂäûÁâ©ÂìÅÂ§±Ë¥•:', error)
              uni.showToast({ title: 'Âà†Èô§Â§±Ë¥•', icon: 'none' })
            }
          }
        }
      })
    }
  }
}
</script>

<style scoped>
