  flex: 1;
}

.switch-desc {
  font-size: 26rpx;
  color: #666;
}

.goods-list {
  display: flex;
  flex-direction: column;
  gap: 20rpx;
}

.good-card {
  background: #fff;
  border-radius: 12rpx;
